<template lang="html">
  <div class="footer">
    <div class="clear">
      <!--左侧，包含各种合作信息和连接-->
      <div class="info left clear">
        <div class="left">
          <dl class="left-dl">
            <dt></dt>
            <dd>
              <a href="#" target="_blank">Contact</a>
            </dd>
            <dd>
              <a href="#" target="_blank">Github</a>
            </dd>
            <dd>
              <a href="#" target="_blank">LinkedIn</a>
            </dd>
          </dl>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'footer_'
}
</script>

<style lang="less" scoped>
.footer {
  background-color: #F5F6F8;

  & > div {
    width: 1100px;
    padding-top: 30px;
    margin: 0 auto;

    .info > div:nth-child(2) {
      height: 70px;
      background-color: #d3d3d3;
      width: 1px;
      margin-top: 40px;
    }

    .info > div:nth-child(3) {
      margin: 50px 0 0 74px;
      width: 240px;
    }

    .left-dl {
      width: 434px;
    }

    .right-dl {
      width: 240px;
    }

    .info dl {
      & dt {
        background: url('../../static/img/footer/name.png') no-repeat;
        background-size: 110px 28px;
        height: 50px;
      }

      & dd {
        float: left;
        width: 120px;
        height: 30px;
        font-size: 14px;

        & a {
          color: #9F9F9F;

          &:hover {
            color: #60ADE8;
          }
        }
      }
    }

    .other-info {
      padding-top: 130px;

      & > div {
        display: flex;
      }

      .partner {
        background: url('../../static/img/footer/partner.png') no-repeat;
        width: 100px;
        height: 35px;
      }

      .partner-top {
        background-position: 0 -458px;
      }

      .partner-under {
        margin-top: 7px;
        background-position: 0 -508px;
      }

      .icp {
        width: 566px;
        margin: 6px 0 0 28px;
        font-size: 12px;
        color: #999;
        line-height: 1.5;

        & > ul {
          width: 50%;
          float: left;

          & a {
            color: #999;

            &:hover {
              color: #60ADE8;
            }
          }
        }
      }
    }

    .footer-right {
      width: 220px;

      .footer-right-ul {

        & > li {
          width: 90px;
          height: 121px;
          position: relative;
          margin-right: 10px;
          float: left;

          .circle {
            margin: 0 auto;
            width: 58px;
            height: 58px;
            border-radius: 50%;
            line-height: 58px;
            border: 1px #dedede solid;
            color: #a7abac;
          }

          .footer-text {
            font-size: 12px;
            color: #999;
            margin-top: 10px;
            text-align: center;
          }
        }
      }
    }
  }
}
</style>



